@charset "UTF-8";
html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-color: #F6F8FF;
  color: #000000;
  font-family: "Roboto", sans-serif;
  line-height: 1.3;
  margin: 0;
}

.page-width {
  max-width: 1100px;
  padding: 0 50px;
  margin: 0 auto;
  width: 100%;
}
@media only screen and (max-width: 990px) {
  .page-width {
    padding: 0 20px;
  }
}

.tickets-app {
  margin-top: 60px;
}

h1, h2, h3 {
  font-weight: 700;
  margin-top: 0px;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1.25rem;
}

.content {
  width: 100%;
  font-family: "Roboto Slab";
}
.content .info {
  width: 100%;
}
.content .info .hide {
  display: none;
}
.content .info .show {
  display: flex;
}
.content .header {
  display: flex;
  justify-content: space-around;
}
.content .header-tab {
  font-weight: bold;
  font-size: 16px;
  line-height: normal;
  padding: 10px;
  cursor: pointer;
  flex: 1;
  text-align: center;
}
@media only screen and (max-width: 549px) {
  .content .header-tab {
    padding: 10px 5px;
  }
}
.content .header-tab.active {
  background-color: black;
  color: #fff;
}
.content .header-tab:after {
  content: "";
  display: block;
  width: 0%;
  height: 3px;
  background-color: #e5dfe3;
  margin-top: 4px;
  transition: 0.4s;
}
.content .header-tab:hover:after {
  width: 60%;
}
.content .tabcontent {
  display: flex;
  width: 100%;
  padding: 10px 20px;
  transition: 0.3s;
  border: 1px solid #ccc;
}
@media only screen and (max-width: 549px) {
  .content .tabcontent {
    width: 100%;
  }
}
.content .tabcontent .description {
  width: 100%;
  margin-top: 30px;
  font-weight: 300;
}
.content .tabcontent .description h3 {
  text-align: center;
  font-size: 26px;
  line-height: normal;
}
.content .tabcontent .description:after {
  content: "";
  display: block;
  width: 0%;
  height: 3px;
  background-color: #c78030;
  margin-top: 30px;
}

.accordion {
  width: 90%;
  max-width: 1000px;
  margin: 2rem auto;
}
@media only screen and (max-width: 549px) {
  .accordion {
    width: 100%;
  }
}
.accordion.out-of-time {
  opacity: 0.5;
  pointer-events: none;
}

.accordion-item {
  background-color: #fff;
  color: #111;
  margin: 1rem 0;
  border-radius: 0.5rem;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.25);
}

.accordion-item-header {
  padding: 0.5rem 3rem 0.5rem 1rem;
  min-height: 3.5rem;
  line-height: 1.25rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
}
.accordion-item-header::after {
  content: "+";
  font-size: 2rem;
  position: absolute;
  right: 1rem;
}

.accordion-item-header.active::after {
  content: "âˆ’";
}

.accordion-item-body {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
}

.accordion-item-body-content {
  padding: 50px 20px 40px;
  line-height: 1.5rem;
  border-top: 1px solid;
  border-image: linear-gradient(to right, transparent, #34495e, transparent) 1;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 20px;
  max-width: 400px;
  margin: 0 auto;
}
@media only screen and (max-width: 549px) {
  .accordion-item-body-content {
    gap: 12px;
    padding: 30px 20px 30px;
  }
}
.accordion-item-body-content .place {
  background-color: #6369836e;
  padding: 4px;
  text-align: center;
  border: 1px solid #303030;
  border-radius: 5px;
  cursor: pointer;
}
.accordion-item-body-content .place.booked {
  opacity: 0.5;
  pointer-events: none;
}

@media (max-width: 767px) {
  html {
    font-size: 14px;
  }
}
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
}
.modal.active {
  display: block;
}
.modal__overlay {
  background-color: #00000035;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.modal__inner {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  max-width: 400px;
  padding: 40px;
  display: flex;
  flex-direction: column;
}
.modal__inner label {
  margin-bottom: 10px;
  text-align: center;
}
.modal__inner input {
  margin-bottom: 10px;
  border-width: 1px;
}
.modal__inner h3 {
  text-align: center;
}