.accordion {
	width: 90%;
	max-width: 1000px;
	margin: 2rem auto;

    @media #{$sm-down}{
		width: 100%;
    }

	&.out-of-time{
		opacity: 0.5;
		pointer-events: none;
	}
}
.accordion-item {
	background-color: #fff;
	color: #111;
	margin: 1rem 0;
	border-radius: 0.5rem;
	box-shadow: 0 2px 5px 0 rgba(0,0,0,0.25);
}
.accordion-item-header {
	padding: 0.5rem 3rem 0.5rem 1rem;
	min-height: 3.5rem;
	line-height: 1.25rem;
	font-weight: bold;
	display: flex;
	align-items: center;
	position: relative;
	cursor: pointer;
	&::after {
		content: "\002B";
		font-size: 2rem;
		position: absolute;
		right: 1rem;
	}
}
.accordion-item-header.active {
	&::after {
		content: "\2212";
	}
}
.accordion-item-body {
	max-height: 0;
	overflow: hidden;
	transition: max-height 0.2s ease-out;
}
.accordion-item-body-content {
	padding: 50px 20px 40px;
	line-height: 1.5rem;
	border-top: 1px solid;
	border-image: linear-gradient(to right, transparent, #34495e, transparent) 1;

	display: grid;
	grid-template-columns: repeat(5, 1fr);
	gap: 20px;
	max-width: 400px;
	margin: 0 auto;

	@media #{$sm-down}{
		gap: 12px;
		padding: 30px 20px 30px;
	}

	.place{
		background-color: $primary;
		padding: 4px;
		text-align: center;
		border: 1px solid $dkGray;
		border-radius: 5px;
		cursor: pointer;

		&.booked{
			opacity: 0.5;
			pointer-events: none;
		}
	}
}
@media (max-width:767px) {
	html {
		font-size: 14px;
	}
}
